@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@200;300;400;500;600;700&display=swap);
:root {
	font-size: 12px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Spartan', 'Roboto', 'Ubuntu', sans-serif;
}

.arrow{background:#000;border-radius:99px}.arrow::after,.arrow::before{content:'';display:block;width:3px;border-radius:99px;height:12px;background:#000;position:relative;left:36px}.arrow::after{top:-13px;-webkit-transform:rotateZ(45deg);transform:rotateZ(45deg)}.arrow::before{top:-8px;-webkit-transform:rotateZ(-45deg);transform:rotateZ(-45deg)}.arrow-thin{height:3px}.arrow-normal{height:4px}.arrow-normal::after,.arrow-normal::before{width:4px}.arrow-normal::before{top:-7px}.arrow-thick{height:5px}.arrow-thick::after,.arrow-thick::before{width:5px}.arrow-thick::before{top:-6px}.arrow-w24{width:24px}.arrow-w24::after,.arrow-w24::before{left:20px}.arrow-w32{width:32px}.arrow-w32::after,.arrow-w32::before{left:28px}.arrow-w40{width:40px}.arrow-w40::after,.arrow-w40::before{left:36px}.arrow-w48{width:48px}.arrow-w48::after,.arrow-w48::before{left:44px}.arrow-w56{width:56px}.arrow-w56::after,.arrow-w56::before{left:52px}.arrow-w64{width:64px}.arrow-w64::after,.arrow-w64::before{left:60px}.arrow-up{-webkit-transform:rotateZ(-90deg);transform:rotateZ(-90deg)}.arrow-down{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.arrow-left{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}

.slideshow__container{position:relative;display:flex;width:100%;height:60vh;min-height:600px}.slideshow__container .slideshow__image{width:60vw;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover}.slideshow__container .slideshow__info{width:40vw;height:100%;background:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:0 80px;position:relative}.slideshow__heading{font-weight:600;font-size:4rem}.slideshow__description{color:#a1a1a1;font-size:1.2rem;line-height:2rem;margin:20px 0}.slideshow__shop-btn{padding:16px;align-self:flex-start;display:flex;align-items:center;cursor:pointer}.slideshow__shop-btn span{font-size:1.4rem;text-transform:uppercase;letter-spacing:4px;margin-right:32px;font-weight:500}.slideshow__shop-btn:hover{color:#a1a1a1}.slideshow__buttons{-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;position:absolute;left:0;bottom:0}.slideshow__buttons .slideshow__left,.slideshow__buttons .slideshow__right{width:5vw;height:5vw;background:#000;cursor:pointer;display:flex;align-items:center;justify-content:center}.slideshow__buttons .slideshow__left:hover,.slideshow__buttons .slideshow__right:hover{background:#454545}@media only screen and (max-width: 900px){.slideshow__container{flex-direction:column;height:auto}.slideshow__container .slideshow__info{width:100%;height:auto;padding:64px 24px}.slideshow__container .slideshow__image{width:100%;height:100vw}.slideshow__container .slideshow__heading{font-size:3rem}.slideshow__container .slideshow__buttons{left:auto;right:0;top:0;min-height:50px;height:10vw;-webkit-transform:translateY(-100%);transform:translateY(-100%);z-index:2}.slideshow__container .slideshow__buttons .slideshow__left,.slideshow__container .slideshow__buttons .slideshow__right{min-height:50px;min-width:50px;height:10vw;width:10vw}}@media only screen and (min-width: 900px) and (max-width: 1200px){.slideshow__container .slideshow__heading{font-size:3rem}.slideshow__container .slideshow__info{padding:16px 32px}}

.additionalInfo__container{width:100%;height:40vh;min-height:350px;display:flex;flex:1 1}.additionalInfo__container .additionalInfo__image{width:30vw;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover}.additionalInfo__container .additionalInfo__info{width:40vw;display:flex;justify-content:center;flex-direction:column;padding:0 64px}.additionalInfo__container .additionalInfo__info .additionalInfo__heading{text-transform:uppercase;letter-spacing:4px;font-weight:700;margin-bottom:24px}.additionalInfo__container .additionalInfo__info .additionalInfo__description{color:#a1a1a1;font-size:1.2rem;line-height:2rem}@media screen and (max-width: 900px){.additionalInfo__container{height:auto;flex-direction:column}.additionalInfo__container .additionalInfo__image{width:100%;height:63.3vw}.additionalInfo__container .additionalInfo__info{width:100%;height:auto;padding:64px 24px}}@media only screen and (min-width: 900px) and (max-width: 1200px){.additionalInfo__container .additionalInfo__info{padding:16px 32px}.additionalInfo__container .additionalInfo__info .additionalInfo__heading{font-size:1.2rem}}

.backdrop{width:100vw;position:fixed;background:#000000e1;z-index:10;left:0;top:0;opacity:0;transition:all 0.5s;height:0}#active{opacity:1;height:100vh}@media only screen and (min-width: 550px){#active{display:none}}

#navbar{position:absolute;width:60vw;height:100px;display:flex;padding:40px;margin:32px;z-index:10;align-items:center}#navbar .menuOpts{display:flex}#navbar .menuOpts a{color:#fff;text-decoration:none;font-size:1.4rem;display:block;margin-left:32px;cursor:pointer;position:relative;text-transform:lowercase;font-weight:600}#navbar .menuOpts a::after{content:'';display:none;position:absolute;width:50%;height:2px;background-color:#fff;-webkit-transform:translate(50%, 3px);transform:translate(50%, 3px)}#navbar .menuOpts a:hover::after{display:block}#navbar img{-webkit-transform:Scale(1.5);transform:Scale(1.5);margin-right:32px}#navbar .hamburger{display:none}@media only screen and (max-width: 550px){#navbar{width:100%;margin:0;justify-items:center}#navbar .menuOpts{position:fixed;top:0;margin:0;background-color:#fff;width:100vw;height:100px;left:0;-webkit-transform:translateY(-100%);transform:translateY(-100%);z-index:11;display:flex;justify-content:space-evenly;align-items:center;transition:all 0.5s}#navbar .menuOpts a{color:#000;font-size:1.1rem}#navbar .menuOpts a::after{content:none}#navbar #activeMenuOpts{-webkit-transform:translateY(0);transform:translateY(0);padding-left:64px}#navbar .hamburger{display:block;position:fixed;z-index:12;padding:20px;margin-left:-20px}#navbar .logo{position:relative;left:50%;-webkit-transform:translateX(-50%) scale(1.5);transform:translateX(-50%) scale(1.5)}}@media only screen and (max-width: 370px){#navbar .menuOpts a{font-size:1rem;margin-left:16px}}

